.main_image {
  width: 100%;
  height: 25rem;
  z-index: -10;
  overflow: hidden;
}

.main_image img {
  width: 110%;
  height: 100%;
  object-fit: cover;
  transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}

.carousel {
  width: 900px;
  height: 500px;
  margin: auto;
}
